              console.log(userInfo);
              // if(userInfo.User.reasonCodeId == -1){
              // if (userInfo.User.state == "READY" || userInfo.User.state == "WORK" || userInfo.User.state == "NOT_READY") {
              //   if (confirm("It seems you already logged in OSC CTI, please click 'Yes' to login here or 'No' to cancel.")) {
