              // }
              // else if(userInfo.User.reasonCode){
              //   if (userInfo.User.reasonCode.category != "LOGOUT") {
              //     if (confirm("It seems you already logged in OSC CTI, please click 'Yes' to login here or 'No' to cancel.")) {
              //       that.defaultLogout(username, password).then((response) => {
