<template>
  <div class="container left status-red" v-if="!systemUp || isError">
    <div class="toast center">{{ systemDownMessage }}</div>
  </div>
  <div class="container left" v-if="activeCall || consultCallActive">
    <table class="table-block margin-right-8px" v-if="activeCall">
      <tr>
        <td class="grey-background p-l-10 p-r-10 p-b-0 p-t-0 bold">
          <table>
            <tr>
              <td>
                <i data-feather="phone-call"></i
                >{{ currentCustomerPhoneNumber }}
              </td>
            </tr>
            <tr>
              <td class="right">
                {{ timerFormat(callTimingHour) }} :
                {{ timerFormat(callTimingMinutes) }} :
                {{ timerFormat(callTimingSeconds) }}
              </td>
            </tr>
          </table>
        </td>

        <td class="p-l-10 p-r-10 p-b-0 p-t-0">
          <button
            title="Call Hold"
            v-show="!onHold"
            v-on:click="putOnHold(true)"
          >
            <img class="icon" src="CTI Icons/pause.png" />
          </button>
          <button
            title="Call Resume"
            v-show="onHold"
            v-on:click="playFromHold(true)"
          >
            <img class="icon resume" src="CTI Icons/resume-call.png" />
          </button>
          <button
            title="Call End"
            v-on:click="callEnd(true)"
            v-bind:disabled="onHold"
            v-show="!consultCallActive"
          >
            <img class="icon end" src="CTI Icons/end-call.png" />
          </button>
        </td>

        <td>
          <input
            type="text"
            maxlength="15"
            class="phone_number"
            v-bind:disabled="onHold"
            v-model="dialedNumber"
          />
        </td>
        <td>
          <button
            title="Call Forward"
            v-on:click="forwardCall"
            v-show="!onHold"
          >
            <img class="icon forward" src="CTI Icons/call-transfer.png" />
          </button>
          <button
            title="Call Consult"
            v-on:click="consultCall"
            v-bind:disabled="false"
            v-show="!consultCallActive && !onHold"
          >
            <img class="icon consult" src="CTI Icons/consult-call.png" />
          </button>
        </td>

        <td class="grey-background p-l-10 p-r-10 p-b-0 p-t-0" v-show="loggedIn">
          <table>
            <tr>
              <td>
                <span class="bold">Queue : </span>
                <span>{{
                  currentQueue
                    ? currentQueue.length > 20
                      ? currentQueue.substring(0, 20) + "..."
                      : currentQueue
                    : ""
                }}</span>
              </td>
            </tr>
            <tr>
              <td>
                <span class="bold">Call Center : </span>
                <span>{{ currentCallCenter }}</span>
              </td>
            </tr>
          </table>
        </td>
      </tr>
    </table>
    <table class="table-block" v-show="consultCallIniated">
      <tr>
        <td class="p-l-10 p-r-10 p-b-0 p-t-0">
          <button title="Call End" v-on:click="callEnd(false)">
            <img class="icon end" src="CTI Icons/end-call.png" />
          </button>
        </td>
      </tr>
    </table>
    <table class="table-block" v-show="consultCallActive">
      <tr>
        <td class="p-l-10 p-r-10 p-b-0 p-t-0" v-show="consultCallActive">
          <button
            title="Call Hold"
            v-show="!consultOnHold"
            v-on:click="putOnHold(false)"
          >
            <img class="icon" src="CTI Icons/pause.png" />
          </button>
          <button
            title="Call Resume"
            v-show="consultOnHold"
            v-on:click="playFromHold(false)"
          >
            <img class="icon resume" src="CTI Icons/resume-call.png" />
          </button>
          <button
            title="Call End"
            v-on:click="callEnd(false)"
            v-bind:disabled="consultOnHold"
          >
            <img class="icon end" src="CTI Icons/end-call.png" />
          </button>
        </td>

        <td
          class="grey-background p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="consultCallActive"
        >
          <table>
            <tr>
              <td><i data-feather="phone-call"></i>{{ consultCallNumber }}</td>
            </tr>
            <tr>
              <td class="right">
                {{ timerFormat(consultCallTimingHour) }} :
                {{ timerFormat(consultCallTimingMinutes) }} :
                {{ timerFormat(consultCallTimingSeconds) }}
              </td>
            </tr>
          </table>
        </td>
      </tr>
    </table>
  </div>
  <div
    v-show="(currentStatus != 'talking'  && wrapUpPending == true  ) || ( campaignWrapUpPending == true)"
    class="container left"
  >
    <table class="table-block">
      <tr>
        <td class="p-l-10 p-b-0 p-t-0 grey-background">
          <table>
            <tr>
              <td>
                <span class="bold center">Contact </span>
              </td>
              <td>
                <select v-model="selectedContactId">
                  <option value="null">Select Contact</option>
                  <option
                    v-for="item in listOfCurrentContacts"
                    v-bind:value="item.id"
                    v-bind:key="item.id"
                  >
                    {{ item.value }}
                  </option>
                </select>
              </td>
            </tr>
            <tr>
              <td>
                <span class="bold center">Incident </span>
              </td>
              <td>
                <select v-model="selectedIncidentId">
                  <option value="null">Select Incident</option>
                  <option
                    v-for="item in listOfCurrentIncident"
                    v-bind:value="item.id"
                    v-bind:key="item.id"
                  >
                    {{ item.value }}
                  </option>
                </select>
              </td>
            </tr>
          </table>
        </td>
        <td v-show="campaignWrapUpPending" style="vertical-align: top;">
          <table>
            <tr>
              <td>
                <span class="bold center">Comments </span>
              </td>
            </tr>
             <tr>
              <td>
                <textarea rows="3" cols="30" v-model="comments">

                </textarea>
              </td>
            </tr>
          </table>
        </td>
         <td v-show="campaignWrapUpPending">
          <table>
            <tr>
              <td>
                <span class="bold center">Disposition </span>
              </td>
            </tr>
             <tr>
              
              <td>
                <select v-model="disposition">
                  <option value="Interested">Interested</option>
                  <option value="Not Interested">Not Interested</option>

                  <option value="Wrong Data">Wrong Data</option>
                  <option value="Will Think">Will Think</option>
                  <option value="Callback">Callback</option>
                  <option value="No Answer">No Answer</option>
                </select>
              </td>
            </tr>
            <tr>
              <td
                class="
                  p-l-10 p-r-10 p-b-0 p-t-0
                  bold
                  grey-background
                  wrapup-button-td
                "
                id="wrapup-td" v-show="campaignWrapUpPending"
              >
                <button
                  id="wrapup-button"
                  class="wrapup-button"
                  v-on:click="
                    loggingCall(null, null, null, 'update', true, null, null)
                  "
                >
                  <!--<img
                    id="logout-icon"
                    class="icon"
                    src="CTI Icons/logout.png"
                  />-->Wrap Up
                </button>
              </td>
            </tr>
          </table>
        </td>
        <td
          class="
            p-l-10 p-r-10 p-b-0 p-t-0
            bold
            grey-background
            wrapup-button-td
          "
          id="wrapup-td" v-show="!campaignWrapUpPending"
        >
          <button
            id="wrapup-button"
            class="wrapup-button"
            v-on:click="
              loggingCall(null, null, null, 'update', true, null, null)
            "
          >
            <!--<img
              id="logout-icon"
              class="icon"
              src="CTI Icons/logout.png"
            />-->Wrap Up
          </button>
        </td>
        <!-- <td class="p-l-10 p-b-0 p-t-0 grey-background">
          <table>
            <tr>
              <td>
                <span class="bold center">Disposition </span>
              </td>
            </tr>
            <tr>
              <td>
                <select>
                  <option value="ready" class="status-green">Will Think</option>
                  <option value="ready" class="status-green">Not Ready</option>
                </select>
              </td>
            </tr>
          </table>
        </td>
        <td class="p-l-10 p-b-0 p-t-0 grey-background">
          <textarea cols="50" rows="3" name="comments" />
        </td> -->
      </tr>
    </table>
  </div>

  <div class="container right container-right">
    <table>
      <tr>
        <td
          class="grey-background p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="(campaignCallAlerting || campaignCallInitiated) && loggedIn"
        >
          <span>
            <p>{{ campaignCallMessage }}</p>
          </span>
        </td>
        <td
          class="grey-background p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="(campaignCallAlerting || campaignCallInitiated) && loggedIn"
        >
          <table>
            <tr>
              <td>
                <span class="bold">Phone Number : </span>
                <span>{{ currentCustomerPhoneNumber }}</span>
              </td>
            </tr>
            <tr>
              <td>
                <span class="bold">Name : </span>
                <span>{{ campaignCallBuddyName }}</span>
              </td>
            </tr>
          </table>
        </td>
        <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="
            loggedIn &&
            !inWrapUpState &&
            !activeCall &&
            (callAlerting || consultCallAlerting)
          "
        >
          <img
            class="icon"
            v-on:click="pickupCall"
            src="CTI Icons/incoming-call.png"
          />
        </td>

        <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="campaignCallAlerting && loggedIn"
        >
          <img
            class="icon"
            v-on:click="pickupCampaignCall"
            src="CTI Icons/incoming-call.png"
          />
        </td>
        <!-- <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="
            loggedIn && !activeCall && (callAlerting || consultCallAlerting)
          "
        >
          <img
            class="icon" v-show="callAlerting"
            v-on:click="callEnd(true)"
            src="CTI Icons/ignore-call.png"
          />
          <img
            class="icon" v-show="consultCallAlerting"
            v-on:click="callEnd(false)"
            src="CTI Icons/ignore-call.png"
          />
        </td> -->
        <td>
          <input
            type="text"
            maxlength="15"
            class="phone_number"
            v-show="
              loggedIn &&
              !inWrapUpState &&
              wrapUpPending == false &&
              !campaignCallAlerting &&
              !campaignCallInitiated &&
              !activeCall &&
              !callAlerting &&
              !consultCallAlerting &&
              !consultCallActive &&
              !outgoingCallInitiated &&
              !campaignWrapUpPending
            "
            v-bind:disabled="false"
            v-model="dialedNumber"
          />
        </td>
        <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="
            loggedIn &&
            !inWrapUpState &&
            wrapUpPending == false &&
            !campaignCallAlerting &&
            !campaignCallInitiated &&
            !activeCall &&
            !callAlerting &&
            !consultCallAlerting &&
            !consultCallActive &&
            !outgoingCallInitiated &&
             !campaignWrapUpPending
          "
        >
          <img
            class="icon"
            v-on:click="outgoingCall($event)"
            src="CTI Icons/outgoing-call.png"
          />
        </td>
        <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="
            loggedIn &&
            !inWrapUpState &&
            !campaignCallAlerting &&
            wrapUpPending == false &&
            !activeCall &&
            !callAlerting &&
            !consultCallAlerting &&
            !consultCallActive &&
            outgoingCallInitiated
          "
        >
          <img
            v-on:click="callEnd(true)"
            class="icon end"
            src="CTI Icons/end-call.png"
          />
        </td>

        <td
          class="p-l-10 p-r-10 p-b-0 p-t-0"
          v-show="(campaignCallAlerting || campaignCallInitiated) && loggedIn"
        >
          <img
            v-on:click="campaignCallEnd()"
            class="icon end"
            src="CTI Icons/end-call.png"
          />
        </td>
        <td class="p-l-10 p-b-0 p-t-0 grey-background bold" v-if="loggedIn">
          <table>
            <tr>
              <td class="bold">{{ fullName }}</td>
            </tr>
            <tr>
              <td>{{ agentExtension }}</td>
            </tr>
          </table>
        </td>

        <td v-show="loggedIn" class="p-l-10 p-b-0 p-t-0 grey-background">
          <table>
            <tr>
              <td>
                <span class="bold center">Status </span>
              </td>
            </tr>
            <tr>
              <td>
                <!-- || currentStatus == 'work' v-bind:disabled="currentStatus == 'talking'" -->
                <select
                  @change="statusChange()"
                  v-model="currentStatus"
                  v-bind:class="{
                    'status-red': currentStatus != 'ready',
                    'status-green': currentStatus == 'ready',
                  }"
                >
                  <option value="ready" class="status-green">Ready</option>
                  <option
                    v-for="item in userStatus"
                    class="status-red"
                    v-bind:value="item.id"
                    v-bind:key="item.id"
                  >
                    {{ item.label }}
                  </option>
                  <option value="work" disabled>Wrap Up</option>
                  <option value="not_ready" disabled>Not Ready</option>
                  <option value="talking" disabled>Talking</option>
                </select>
              </td>
            </tr>
          </table>
        </td>
        <td v-show="loggedIn" class="p-0 grey-background">
          <table class="logout-button-table">
            <tr>
              <td
                class="
                  p-l-10 p-r-10 p-b-0 p-t-0
                  bold
                  grey-background
                  logout-button-td
                "
                id="logout-td"
              >
                <button
                  id="logout-button"
                  class="logout-button"
                  v-show="loggedIn"
                  v-bind:disabled="
                    currentStatus == 'talking' || currentStatus == 'work'
                  "
                  v-on:click="logoutClicked = true"
                >
                  <!--<img
                    id="logout-icon"
                    class="icon"
                    src="CTI Icons/logout.png"
                  />-->Logout
                </button>
              </td>
            </tr>
            <tr>
              <td>
                <!--<select required v-model="currentLogoutStatus" v-bind:disabled="currentStatus == 'talking'" oninvalid="this.setCustomValidity('Please select a Logout Reason')" onchange="this.setCustomValidity('')">-->
                <select
                  id="logout-select"
                  required
                  v-model="currentLogoutStatus"
                  v-bind:disabled="currentStatus == 'talking'"
                  v-show="logoutClicked"
                  @change="logout()"
                >
                  <option value="0" disabled>Logged In</option>
                  <option
                    v-for="item in logoutStatus"
                    v-bind:value="item.id"
                    v-bind:key="item.id"
                  >
                    {{ item.label }}
                  </option>
                </select>
              </td>
            </tr>
          </table>
        </td>
        <td v-show="systemUp && !loggedIn" class="p-l-10 p-r-10 p-b-0 p-t-0">
          <button v-on:click="openLogin">
            <img class="icon" src="CTI Icons/login-icon.png" />
          </button>
        </td>
      </tr>
    </table>
  </div>
  <div class="loading" v-show="loading" id="loading">Loading&#8230;</div>
</template>
<script src="./script.js"></script>
